@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Merriweather:wght@300&display=swap);
* {
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fafafa;
  overflow-x: hidden;
  position: relative;
  background: url(/portfolio/static/media/bg.54815803.png) #fafafa;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --main-color: #ffc500;
}

.top_60 {
  margin-top: 60px !important;
}
@media (max-width: 901px) {
  .top_60 {
    margin-top: 30px;
  }
}
.container_shadow {
  box-shadow: 0px 2px 92px 0px rgba(245, 0, 0, 0.13);
  -webkit-box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.13);
  -moz-box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.13);
}

.main_content {
  background-color: white;
  min-height: 100vh;
  border-radius: 6px;
  -moz-border-radius: 6px;
  -webkit-bok-radius: 6px;
  margin-bottom: 30px;
  padding: 20px;
}

.timeline {
  padding: 0 !important;
}

.timeline .timeline_header {
  padding-top: 7px;
}
.timeline .timeline_first_item {
  min-height: 80px !important;
}
.timeline .MuiTimelineItem-root {
  min-height: 0px;
}
.timeline .MuiTimelineItem-missingOppositeContent:before {
  display: none;
}
.timeline .timeline_dot_header {
  color: black !important;
  background-color: var(--main-color);
  font-size: small !important;
  padding: 10px !important;
}
.timeline .timeline_dot {
  color: black !important;
  border-color: var(--main-color);
  padding: 2px !important;
}

.timeline_header .MuiTimelineConnector:root {
  background-color: #eee;
}
.timeline .timeline_dot_header > .MuiSvgIcon-root {
  font-size: 20px;
  color: darkslategray;
}
.timeline .seperator_padding {
  padding-left: 18px !important;
}
.timeline .timeline_content {
  padding-top: 0px !important;
  margin-bottom: 20px;
}

.profile {
  background-color: white;
  border-radius: 6px;
  width: 100%;
  display: inline-block;
}

.profile_name {
  line-height: 18px;
  padding: 20px;
}
.profile_name .name {
  text-transform: uppercase;
  font-size: 17px;
  font-weight: bold;
}
.profile_name .title {
  font-size: 13px;
  color: #777;
}
.profile_image {
  margin-top: -10px;
  -webkit-clip-path: polygon(0 9%, 100% 0, 100% 94%, 0% 100%);
  clip-path: polygon(0 9%, 100% 0, 100% 94%, 0% 100%);
}
.profile_image .img {
  width: 100%;
}
.profile_information {
  top: 0;
  left: 0;
  margin: -40px 10px 30px;
}

.profile_information .timeline .timeline_content {
  margin-top: 2px;
  margin-bottom: 12px;
}
.timelineitem_text {
  font-size: 13.5px !important;
  color: #787878;
  font-weight: 400 !important;
}

.timelineitem_text > span {
  color: black;
}
.timelineitem_text > a {
  text-decoration: none;
  color: #787878;
}

.profile .btn_container {
  width: 100%;
  display: -webkit-flex !important;
  display: flex !important;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 15px;
}

.custom_btn {
  background-color: var(--main-color) !important;
  font-size: 14px;
  font-weight: 500;
  border: 0;
  border-radius: 50px !important;
  padding: 1px 12px;
  line-height: 35px;
  min-width: 100px;
  outline: none;
}

.custom_btn .btn_icon_container {
  color: black;
  background-color: #fff;
  border-radius: 50px !important;
  height: 27px;
  width: 27px;
  line-height: 29px;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.custom_btn .btn_icon_container > .MuiSvgIcon-root {
  font-size: 16px;
}
.custom_btn .btn_text {
  font-size: 14px;
  text-transform: none;
  text-align: center;
  width: 100%;
  margin-right: 5px;
  margin-left: 5px;
}

.header {
  margin-bottom: 30px !important;
  background-color: white;
  padding: 0 22px 0 0 !important;
  border-radius: 6px !important;
  box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07) !important;
}

.header .header_link {
  font-weight: 500;
  color: darkslategray !important;
}
.header .header_link:hover {
  color: var(--main-color) !important;
}
.header .header_link_active {
  font-weight: 500;
  color: var(--main-color) !important;
}
.header .header_navlink {
  padding: 0;
}
.header .header_home {
  background-color: var(--main-color);
  padding: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center im !important;
          justify-content: center im !important;
  -webkit-align-items: center im !important;
          align-items: center im !important;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}
.header .header_left {
  margin-right: auto;
}
.header .header_right {
  display: -webkit-flex !important;
  display: flex !important;
  -webkit-align-items: center !important;
          align-items: center !important;
}
.header .header_right > a > .MuiSvgIcon-root {
  color: black;
  font-size: 22px;
  margin-right: 12px;
}

.footer {
  background-color: #1e1e1e;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  min-height: 50px;
  border-radius: 8px;
  padding: 20px 25px;
}

.footer .footer_copyright {
  font-size: small;
  text-align: end;
  color: #9c9c9c;
}
.footer .footer_copyright > a {
  text-decoration: none;
  color: var(--main-color);
}
.footer .footer_name {
  font-family: "Kaushan Script";
}

.footer .footer_name > span {
  color: var(--main-color);
}

